<template>
  <v-row>
    <v-col
      cols="12"
      md="6"
      offset-md="3"
      class="pa-4"
    >
      <v-card>
        <v-card-text>
          <v-row>
            <v-col
              cols="12"
              md="6"
            >
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    outlined
                    block
                    color="primary"
                    @click="copyToClipboard('https://chrome.google.com/webstore/detail/dolphin/dihicliaoakcfiokadjibcobbemgdbgg')" 
                    v-on="on"
                  >
                    <v-icon
                      class="mr-1"
                      :size="18"
                    >
                      fab fa-chrome
                    </v-icon>
                    chrome
                  </v-btn>
                </template>
                <span>{{ $t('common.copy') }}</span>
              </v-tooltip>
            </v-col>
            <v-col
              cols="12"
              md="6"
            >
              <v-btn
                href=""
                target="_blank"
                outlined
                block
                color="orange"
                disabled
              >
                <v-icon
                  class="mr-1"
                  :size="18"
                >
                  fab fa-firefox
                </v-icon>
                firefox (coming soon)
              </v-btn>
            </v-col>

            <v-col cols="12">
              <v-text-field
                :value="dolphinToken"
                :label="$t('browserExtension.token')"
                outlined
                dense
                hide-details
                @focus="$event.target.select()"
              >
                <template #append>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on }">
                      <v-btn
                        icon
                        x-small
                        @click="copyToClipboard(dolphinToken)"
                        v-on="on"
                      >
                        <v-icon>content_copy</v-icon>
                      </v-btn>
                    </template>
                    <span>{{ $t('common.copy') }}</span>
                  </v-tooltip>
                </template>
              </v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col
      cols="12"
      md="6"
      offset-md="3"
      class="pa-4"
    >
      <v-card>
        <a
          href="https://anty.dolphin.ru.com/?utm_source=ecosystem&utm_medium=cpc&utm_campaign=dolphin_app&utm_content=browser_extention_banner"
          target="_blank"
        >
          <v-img
            contain
            :src="banner"
          />
        </a>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
import clipboard       from '@/mixins/clipboard';
import Cookies         from 'js-cookie';
import {mapGetters}    from 'vuex';

export default {
  name: 'BrowserExtension',

  mixins: [clipboard],

  computed: {
    ...mapGetters({
      profile: 'main/profile'
    }),

    dolphinToken() {
      const baseUrl = location.protocol + '//' + location.hostname + '/new';
      let authorization = `${Cookies.get('user_id')}-${Cookies.get('hash')}`;
      return window.btoa(baseUrl + '::' + authorization);
    },

    banner() {
      return  (
        'https://anty-assets.s3.eu-central-1.amazonaws.com/banners/dolphin-15-11-2021/' 
        + (this.$i18n.locale === 'ru' ? 'ru' : 'en')
        + '-'
        + (this.$vuetify.theme.isDark ? 'dark' : 'light')
        + '.png'
      );
      // if (this.profile && this.profile.language === 'ru') {
      //   return require('@/assets/images/bannerRu.png');
      // }
      // return require('@/assets/images/bannerEn.png');
    }
  }
};
</script>